import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Nav from '../components/Nav.vue'
import Brands from '../components/Brands.vue'
import Products from '../components/Products.vue'
import About from '../components/About.vue'
import Contact from '../components/Contact.vue'
import Hymax from '../components/hymaxproducts.vue'
import Threet from '../components/threetproducts.vue'
import Main from '../components/Main.vue'
import Ishine from '../components/ishineproducts.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/Nav',
    name: 'Nav',
    component: Nav
  },
  {
    path: '/Brands',
    name: 'Brands',
    component: Brands
  },
  {
    path: '/Products',
    name: 'Products',
    component: Products
  },
  {
    path: '/About',
    name: 'About',
    component: About
  },
  {
    path: '/Contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/Threet',
    name: 'Threet',
    component: Threet
  },
  {
    path: '/Hymax',
    name: 'Hymax',
    component: Hymax
  },
  {
    path: '/ishine',
    name: 'Ishine',
    component: Ishine
  },
  {
    path: '/',
    name: 'Main',
    component: Main
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
